import { Injectable } from '@angular/core';
import {Http, Response} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';

import { Post } from './post.model';
import { Param } from './param';

@Injectable()
export class PostService {

    private api_url = "https://api-haidangdev.herokuapp.com/api/blog/";

    constructor(private _http:Http) { }

    getPosts(cond?: Param): Observable<Post[]>{
        let uri = this.api_url + 'post/list';
        cond.skip ? uri += '?skip=' + cond.skip : '';
        cond.limit ? uri += '?&limit=' + cond.limit : '';
        return this._http.get(uri)
		.map((response: Response) => <Post[]> response.json())
        .catch(this.handleError);
    }

    getPostDetail(slug): Observable<Post>{
        return this._http.post(this.api_url + 'post/detail', {postSlug: slug})
        .map((response: Response) => <Post> response.json())
        .catch(this.handleError);
    }

    private handleError(error: Response){
        console.log(error);
        return Observable.throw(error.json().error || 'Server Error');
    }
}


