import {Component, OnInit, AfterViewInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import * as $ from 'jquery';
import {PostService} from './post.service';
import {Post} from './post.model';

@Component({moduleId: module.id, selector: 'postDetail', templateUrl: 'post-detail.component.html'})
export class PostDetailComponent implements OnInit,
AfterViewInit {
    public post : Post;
    constructor(private _postService : PostService, private _route : ActivatedRoute, private _router : Router) {}

    date(date) {
        let d = new Date(date);
        let month = [
            'JAN',
            'FEB',
            'MAR',
            'APR',
            'MAY',
            'JUN',
            'JUL',
            'AUG',
            'SEP',
            'OCT',
            'NOV',
            'DEC'
        ];
        return {
            year: d.getFullYear(),
            month: month[d.getMonth()],
            date: d.getDate()
        }
    }

    ngOnInit() {
        $('body').scrollTop(0);
        let slug = this._route.snapshot.params['slug'];

        this
            ._postService
            .getPostDetail(slug)
            .subscribe((post) => {
                this.post = post;
            });
    }

    ngAfterViewInit() {}

}


