import { Component, OnInit } from '@angular/core';
import { PostService } from '../../post/post.service';
import { Post } from '../../post/post.model';
import { AppComponent } from '../../app.component';

@Component({
    moduleId: module.id,
    selector: 'listPost',
    templateUrl: 'list-post.html'
})
export class ListPostComponent extends AppComponent implements OnInit {
    
    posts: Post[];
    param = {
        skip: 0,
        limit: 9
    }
    
    constructor(private _postService: PostService) { 
        super();
    }

    onViewMore(){
        this._postService.getPosts(this.param)
        .subscribe((posts: Post[]) => {
            this.param.skip += this.param.limit;
            this.posts.concat(posts);
        });
    }

    ngOnInit() { 
        this._postService.getPosts(this.param)
        .subscribe((posts: Post[]) => {
            this.param.skip += this.param.limit;
            this.posts = posts;
        })
    }
}


